body {
  background: #1c1b1d;
  display: flex;
  flex-direction: column;
  margin: 0;
  height: 100%;
  font-family: "Barlow", sans-serif;
  font-size: 14px;
  line-height: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.intro {
  background: #1c1b1d;
  height: 100vh;
  position: relative; }
  .intro .content {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    left: 10%;
    position: absolute;
    top: 10vh;
    width: 80%; }
    .intro .content > .name {
      font-family: "Montserrat", sans-serif;
      font-size: 60px;
      margin: 0 auto 40px auto;
      letter-spacing: 0.1em;
      line-height: 60px;
      text-shadow: 5px 5px #5c6bc0, 20px 20px #1c1b1d;
      -webkit-text-fill-color: transparent;
      -webkit-text-stroke-width: 3px;
      -webkit-text-stroke-color: #ebeaec; }
    .intro .content > article {
      color: white;
      display: flex;
      height: 25px; }
      .intro .content > article a {
        color: #ebeaec; }
      .intro .content > article > span {
        font-family: "Source Code Pro", sans-serif;
        font-size: 20px;
        font-weight: 600;
        margin: 0 30px 0 30px; }
    .intro .content > .info-cards {
      height: calc(90vh - 180px);
      margin: 50px 0 0 0;
      overflow-y: scroll;
      padding: 0 50px 0 50px;
      width: 550px; }
      .intro .content > .info-cards > div {
        margin-right: -7.5px; }

.content-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background: #ebeaec;
  display: flex;
  margin-bottom: 20px;
  min-height: 100px;
  opacity: 0.8;
  padding: 15px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 100%; }
  .content-card article.image-col {
    margin-right: 20px; }
    .content-card article.image-col img {
      padding: 10px;
      width: 100px; }
  .content-card article.text-col {
    color: #1c1b1d;
    font-family: "Barlow", sans-serif;
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
    position: relative;
    width: 100%; }
    .content-card article.text-col .title {
      font-weight: 900; }
    .content-card article.text-col .tags {
      margin-top: 20px; }

.simple-tag {
  border: 1.5px solid #201f21;
  border-radius: 4px;
  color: #1c1b1d;
  display: inline-flex;
  font-family: "Source Code Pro", sans-serif;
  font-size: 11px;
  font-weight: 600;
  margin: 0 5px 5px 0;
  padding: 1px 4px 1px 4px; }

